.scrollbar {
  background: inherit;
  display: flex;
  border-color: transparent;
  visibility: visible;
  opacity: 1;
  flex: none;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  transition: opacity 0.1s linear;
  -moz-transition: opacity 0.1s linear;
  -webkit-transition: opacity 0.1s linear;
  /* ScrollButton */
  /* ScrollbarTrack */
}
.scrollbar.vertical {
  flex-direction: column;
}
:global(.enact-locale-right-to-left) .scrollbar.horizontal {
  direction: ltr;
  transform: scaleX(-1);
}
.scrollbar .scrollbarButtonNoCompositeLayer,
.scrollbar .scrollbarButtonNoCompositeLayer:before,
.scrollbar .scrollbarButtonNoCompositeLayer:after,
.scrollbar .scrollbarButtonNoCompositeLayer div,
.scrollbar .scrollbarButtonNoCompositeLayer span {
  will-change: auto;
}
.scrollbar .scrollbarButtonNoCompositeLayer:before {
  content: none;
}
.scrollbar .scrollbarButtonNoCompositeLayer div,
.scrollbar .scrollbarButtonNoCompositeLayer span {
  -webkit-transform: initial;
  transform: initial;
}
.scrollbar .scrollButton {
  background-color: transparent;
  margin: 0;
}
.scrollbar .scrollButton,
.scrollbar .scrollButton:before,
.scrollbar .scrollButton:after,
.scrollbar .scrollButton div,
.scrollbar .scrollButton span {
  will-change: auto;
}
.scrollbar .scrollButton:before {
  content: none;
}
.scrollbar .scrollButton div,
.scrollbar .scrollButton span {
  -webkit-transform: initial;
  transform: initial;
}
.scrollbar .scrollbarTrackShown {
  opacity: 1;
}
:global(.carbon) .scrollbar ::before {
  background-color: #373737;
}
:global(.carbon) .scrollbar.vertical ::before {
  width: inherit;
}
:global(.carbon) .scrollbar.horizontal ::before {
  height: inherit;
}
:global(.carbon) .scrollbar.corner {
  -webkit-padding-end: 60px;
