/*
 * Provide the height of the sprite and an index value of how far to vertically offset this sprite
 * 0 for the first position (top), 1 below that, 2 below 1, etc.
 * This way, selected/focused states can refer to other sprite indexes easily.
 * 0 for normal/resting
 * 1 for focused
 */
.icon {
  --agate-icon-sprite-count: 1;
}
.icon:global(.carbon) {
  width: 48px;
  height: 48px;
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 48px * -0);
  background-size: 48px calc(var(--agate-icon-sprite-count, 1) * 48px);
  background-repeat: no-repeat;
  margin: 12px;
  font-family: "Agate Icons";
  font-size: 96px;
  line-height: 48px;
  text-align: center;
  position: relative;
  color: inherit;
  letter-spacing: inherit;
  text-indent: initial;
}
.icon:global(.carbon).huge {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 190px * -0);
  background-size: 190px calc(var(--agate-icon-sprite-count, 1) * 190px);
  width: 190px;
  height: 190px;
  font-size: 320px;
  line-height: 190px;
}
.icon:global(.carbon).small {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 36px * -0);
  background-size: 36px calc(var(--agate-icon-sprite-count, 1) * 36px);
  width: 36px;
  height: 36px;
  font-size: 72px;
  line-height: 36px;
}
.icon:global(.carbon).smallest {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 21px * -0);
  background-size: 21px calc(var(--agate-icon-sprite-count, 1) * 21px);
  width: 21px;
  height: 21px;
  font-size: 42px;
  line-height: 21px;
}
.icon:global(.carbon).dingbat {
  font-family: "LG Display_Dingbat";
  font-size: 48px;
  line-height: 40px;
}
.icon:global(.carbon).dingbat.small {
  font-size: 36px;
  line-height: 32px;
}
.icon:global(.carbon):global(.spottable):focus {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 48px * -1);
}
.icon:global(.carbon):global(.spottable):focus.huge {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 190px * -1);
}
.icon:global(.carbon):global(.spottable):focus.small {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 36px * -1);
}
.icon:global(.carbon):global(.spottable):focus.smallest {
  background-position: center calc((var(--agate-icon-sprite-count, 1) - 1) * 21px * -1);
}
